import React from 'react'
import Select from 'react-select'
import AUSelect from '@gov.au/select'

export default class DHQuantitySelector extends React.Component {

    defaultOptions = [ 
        { value: '0', text: '0' }, 
        { value: '1', text: '1' },
        { value: '2', text: '2' },
        { value: '3', text: '3' },
        { value: '4', text: '4' },
        { value: '5', text: '5' },
        { value: '6', text: '6' },
        { value: '7', text: '7' },
        { value: '8', text: '8' },
        { value: '9', text: '9' },
        { value: '10', text: '10' },
        { value: '10+', text: '10+' },
    ]

    options = this.props.options || this.defaultOptions

    state = {
        selectedOption: null
    }

    handleChange = (selectedOption) => {
        this.setState({ selectedOption })
    }

    render () {
        const { selectedOption } = this.state

        return(
            <div>
                <AUSelect options={this.options} />
            </div>            
        )
    }
}



